import React, { Component } from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router-dom";

import Button from "react-bootstrap/Button";
import Form from "react-bootstrap/Form";

import Input from "../../components/UI/Input/Input";
import Spinner from "../../components/UI/Spinner/Spinner";
import images from "../../imageList";

import * as actions from "../../store/actions";

import "./Auth.css";

class Auth extends Component {
  state = {
    controls: {
      email: {
        elementType: "input",
        elementConfig: {
          type: "email",
          placeholder: "Email Address",
        },
        value: "",
        label: "Email",
        validation: { required: true, isEmail: true },
        valid: false,
      },
      password: {
        elementType: "input",
        elementConfig: {
          type: "password",
          placeholder: "Password",
        },
        value: "",
        label: "Password",
        validation: { required: true, minLength: 6 },
        valid: false,
      },
    },
    isSignUp: false,
  };

  componentDidMount() {
    if (!this.props.buildingBurger && this.props.authRedirectPath !== "/") {
      this.props.onSetAuthRedirectPath();
    }
  }

  checkValidity(value, rules) {
    let isValid = true;
    if (rules.required) {
      isValid = value.trim() !== "" && isValid;
    }
    if (rules.minLength) {
      isValid = value.length >= rules.minLength && isValid;
    }
    if (rules.maxLength) {
      isValid = value.length <= rules.maxLength && isValid;
    }
    return isValid;
  }
  inputChangedHandler = (event, controlName) => {
    const updatedControls = {
      ...this.state.controls,
      [controlName]: {
        ...this.state.controls[controlName],
        value: event.target.value,
        valid: this.checkValidity(
          event.target.value,
          this.state.controls[controlName].validation
        ),
      },
    };

    this.setState({ controls: updatedControls });
  };

  submitHandler = (event) => {
    event.preventDefault();
    this.props.onAuth(
      this.state.controls.email.value,
      this.state.controls.password.value,
      this.state.isSignUp
    );
  };

  switchAuthModeHandler = () => {
    this.setState((prevState) => {
      return {
        isSignUp: !prevState.isSignUp,
      };
    });
  };

  render() {
    const formElementsArray = [];
    for (let key in this.state.controls) {
      formElementsArray.push({ id: key, config: this.state.controls[key] });
    }

    let form = (
      <Form onSubmit={this.submitHandler}>
        <img id="pattern" src={images.pattern} alt="pattern" />
        {formElementsArray.map((formElement) => {
          let required = false;
          let minLength = "";
          let maxLength = "";
          if (formElement.config.validation !== undefined) {
            if (formElement.config.validation.required) {
              required = true;
            }
            if (formElement.config.validation) {
              if (formElement.config.validation.minLength) {
                minLength += formElement.config.validation.minLength.toString();
              }
              if (formElement.config.validation.maxLength) {
                maxLength = formElement.config.validation.maxLength.toString();
              }
            }
          }
          return (
            <Input signIn
              key={formElement.id}
              label={formElement.config.label}
              elementType={formElement.config.elementType}
              elementConfig={formElement.config.elementConfig}
              value={formElement.config.value}
              required={required}
              invalid={!formElement.config.valid}
              minLength={minLength}
              maxLength={maxLength}
              changed={(event) =>
                this.inputChangedHandler(event, formElement.id)
              }
            />
          );
        })}
        <Button
          className="spaced"
          id="button-order"
          type="submit"
          variant="success"
          style={{ width: "110px" }}
        >
          {this.state.isSignUp ? "SIGN UP" : "LOGIN"}
        </Button>
        <p
          onClick={this.switchAuthModeHandler}
          style={{
            display: "block",
            fontSize: "small",
            color: " #d16f3e",
            textDecoration: "underline",
            margin: "10px auto",
            width: "150px",
          }}
        >
          Switch to {this.state.isSignUp ? "Login" : "Sign Up"}
        </p>
      </Form>
    );

    if (this.props.loading) {
      form = <Spinner />;
    }

    let errorMessage = null;
    if (this.props.error) {
      errorMessage = <p>{this.props.error.message}</p>;
      console.log(this.props.error.message);
    }

    let authRedirect = null;
    if (this.props.isAuthenticated) {
      authRedirect = <Redirect to={this.props.authRedirectPath} />;
    }

    return (
      <div className="Auth">
        {authRedirect}
        <div className="SignIn">
          {errorMessage}
          {form}
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loading: state.auth.loading,
    error: state.auth.error,
    isAuthenticated: state.auth.token !== null,
    buildingBurger: state.burger.building,
    authRedirectPath: state.auth.authRedirectPath,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    onAuth: (email, password, isSignUp) =>
      dispatch(actions.auth(email, password, isSignUp)),
    onSetAuthRedirectPath: () => dispatch(actions.setAuthRedirectPath("/")),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);
