import React from "react";

import Form from "react-bootstrap/Form";
import "./Input.css";

const Input = (props) => {
  let inputEl = null;

  let style = {
    width: "100%"
  };
  if(props.signIn){
    style = {
      width: "95%"
    }
  }

  switch (props.elementType) {
    case "input":
      inputEl = (
        <Form.Control style={style}
          isInvalid={props.valid}
          {...props.elementConfig}
          value={props.value}
          required={props.required}
          minLength={props.minLength}
          maxLength={props.maxLength}
          onChange={props.changed}
        />
      );
      break;
    case "select":
      inputEl = (
        <Form.Control as="select" value={props.value} onChange={props.changed}>
          {props.elementConfig.options.map((option) => (
            <option key={option.value} value={option.value}>
              {option.displayValue}
            </option>
          ))}
        </Form.Control>
      );
      break;
    default:
  }
  return (
    <div>
      <Form.Group className="input-form">
        <Form.Label className="label">{props.label}</Form.Label>
        {inputEl}
      </Form.Group>
    </div>
  );
};

export default Input;
